@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';


.mainBlock {
  background-color: $bgColor;
  min-height: 100vh;
  @include flex(row, center, center, nowrap);
  padding: 100px 0;

  .particles {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.3;
  }

  .container {
    @include container();
    @include flex(row, space-around, center, nowrap);

    .greeting {
      @include flex(column, flex-start, flex-start, nowrap);

      span {
        font-size: 16px;
        color: $textColor;

        &:nth-child(2) {
          @include text($titleFontFamily, 50px, 1.2, 700);
          letter-spacing: 5px;
          margin: 10px 0;
          font-weight: normal;

          span {
            @include text($titleFontFamily, 50px, 1.2, 700);
            letter-spacing: 5px;
            position: relative;
            z-index: 0;
            display: inline-block;
            font-weight: normal;

            &:before {
              content: '';
              position: absolute;
              width: 100%;
              height: 20px;
              background-color: $primaryColor;
              bottom: 0;
              z-index: -1;

            }
          }
        }
        &:nth-child(3) {
          @include text($textFontFamily, 28px, 1.8, 400);
          margin: 0;
          color: $textColor;
        }
      }



      @media screen and (max-width: 1143px) {
        align-items: center;
      }
    }

    .photo {
      width: 300px;
      height: 400px;
      background-color: #f8f8f8;
      position: relative;
      z-index: 1;
      @media screen and (max-width: 1143px) {
        margin-top: 50px;
      }

      .image {
        height: 400px;
        width: 300px;
        @include bgSettings();
        // Placeholder background - replace with your photo
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        // background-image: url('../img/your-photo.jpg');
        background-position: 80% 0;
        @media screen and (max-width: 600px) {
          height: 360px;
          width: 260px;
          transition: .5s;
        }

        &:before {
          content: '';
          position: absolute;
          height: 110%;
          width: 105%;
          border: 5px solid $primaryColor;
          left: 15px;
          top: -15px;
          z-index: -1;
        }
      }
    }
  }
}




